function VideoGrid({ videos, onSelectVideo }: { videos: Video[], onSelectVideo: (video: Video) => void }) {
  const parentRef = useRef<HTMLDivElement>(null);
  
  // Calculate columns based on screen size
  const getColumns = () => {
    if (!parentRef.current) return 1;
    const width = parentRef.current.offsetWidth;
    if (width >= 1536) return 4; // 2xl
    if (width >= 1024) return 3; // lg
    if (width >= 640) return 2;  // sm
    return 1; // mobile
  };

  const rowVirtualizer = useVirtualizer({
    count: Math.ceil(videos.length / getColumns()),
    getScrollElement: () => parentRef.current,
    estimateSize: () => 380, // Adjusted base height
    overscan: 5,
    horizontal: false,
  });

  return (
    <div ref={parentRef} className="h-[calc(100vh-200px)] overflow-auto px-4">
      <div
        style={{
          height: `${rowVirtualizer.getTotalSize()}px`,
          position: 'relative',
          width: '100%',
        }}
      >
        {rowVirtualizer.getVirtualItems().map((virtualRow) => {
          const columns = getColumns();
          const firstIndex = virtualRow.index * columns;
          const videoSlice = videos.slice(firstIndex, firstIndex + columns);

          return (
            <motion.div
              key={virtualRow.index}
              style={{
                position: 'absolute',
                top: 0,
                left: 0,
                width: '100%',
                transform: `translateY(${virtualRow.start}px)`,
              }}
            >
              <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 2xl:grid-cols-4 gap-6">
                {videoSlice.map((video) => (
                  <div
                    key={video.id}
                    onClick={() => onSelectVideo(video)}
                    className="group bg-white rounded-lg shadow-md hover:shadow-lg transition-all duration-200 overflow-hidden"
                  >
                    <div className="relative aspect-video">
                      <Image
                        src={`https://img.youtube.com/vi/${video.youtubeId}/hqdefault.jpg`}
                        alt={video.title}
                        className="object-cover w-full h-full transition-transform duration-300 group-hover:scale-105"
                        width={640}
                        height={360}
                        priority={video.id <= 6}
                        loading={video.id <= 6 ? "eager" : "lazy"}
                        quality={85}
                        sizes="(max-width: 640px) 100vw, (max-width: 1024px) 50vw, (max-width: 1536px) 33vw, 25vw"
                      />
                      <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity duration-300 flex items-center justify-center">
                        <FaPlay className="w-10 h-10 text-white" />
                      </div>
                    </div>

                    <div className="p-4">
                      <h3 className="font-semibold text-lg mb-2 line-clamp-1 text-gray-900">
                        {video.title}
                      </h3>
                      <p className="text-gray-600 text-sm mb-3 line-clamp-2 min-h-[2.5rem]">
                        {video.description}
                      </p>
                      <div className="flex items-center justify-between text-sm text-gray-500">
                        <span className="flex items-center gap-2">
                          <FaYoutube className="text-red-600 w-5 h-5" />
                          <span>YouTube</span>
                        </span>
                        <span className="text-gray-400">
                          {new Date(video.date).toLocaleDateString(undefined, {
                            year: 'numeric',
                            month: 'short',
                            day: 'numeric'
                          })}
                        </span>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </motion.div>
          );
        })}
      </div>
    </div>
  );
}

export default function VideosPage() {
  const [currentCategory, setCurrentCategory] = useState<string>('All');
  const [selectedVideo, setSelectedVideo] = useState<Video | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  const categories = ['All', ...new Set(videos.map(video => video.category))];
  const filteredVideos = currentCategory === 'All' 
    ? videos 
    : videos.filter(video => video.category === currentCategory);


  return (
    <div className="container mx-auto px-4">
      <main>
        <h1 className="text-3xl font-bold mb-8 text-center">Video Gallery</h1>
        
        {/* Category Filter */}
        <div className="flex flex-wrap justify-center gap-4 mb-8">
          {categories.map(category => (
            <button
              key={category}
              onClick={() => setCurrentCategory(category)}
              className={`px-4 py-2 rounded-lg transition-all duration-200 ${
                currentCategory === category
                  ? 'bg-gray-900 text-white'
                  : 'bg-gray-100 hover:bg-gray-200 text-gray-700'
              }`}
            >
              {category}
            </button>
          ))}
        </div>

        <Suspense 
          fallback={
            <div className="animate-pulse grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
              {[...Array(6)].map((_, i) => (
                <div key={i} className="bg-gray-200 rounded-xl h-[350px]" />
              ))}
            </div>
          }
        >
          <VideoGrid videos={filteredVideos} onSelectVideo={setSelectedVideo} />
        </Suspense>

        {/* Video Modal */}
        {selectedVideo && (
          <div 
            className="fixed inset-0 z-50 bg-black/90 flex items-center justify-center p-4"
            onClick={() => setSelectedVideo(null)}
          >
            <div 
              className="relative w-full max-w-6xl aspect-video"
              onClick={e => e.stopPropagation()}
            >
              {isLoading && (
                <div className="absolute inset-0 flex items-center justify-center">
                  <div className="animate-spin rounded-full h-12 w-12 border-4 border-white border-t-transparent" />
                </div>
              )}
              <iframe
                src={`https://www.youtube.com/embed/${selectedVideo.youtubeId}?autoplay=1&rel=0`}
                title={selectedVideo.title}
                className="w-full h-full"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
                onLoad={() => setIsLoading(false)}
              />
              <button
                onClick={() => setSelectedVideo(null)}
                className="absolute -top-10 right-0 text-white hover:text-gray-300"
              >
                Close
              </button>
            </div>
          </div>
        )}
      </main>
    </div>
  );
}